@import "../../../../css/module/fn";
@import "../../../../css/module/color";



#main{
	
	
	h1{
		margin-bottom: 1em;
		font-size: 20px;
	}
	h3{
		height: 2em;
		line-height: 2em;
		margin-bottom: 0;
		color: $red;
		text: {
			indent: 38px;

		}
		font: {
			size: 14px;
			weight: 600;
		}
		background:{
			repeat: no-repeat;
			position: 14px center;
			size: 14px 14px ;
		}
		
		&.h3type {
			background-image: url("./ico32-bloom.png");
		}
		&.h3time {
			background-image: url("./ico32-clock.png");
		}
		&.h3note {
			background-image: url("./ico32-book.png");
		}
		&.h3location {
			background-image: url("./ico32-meeting.png");
		}
	}
	
	.txt{
		text-indent: 38px;
		margin-top: -1em;
	}
	.pMsg{
		margin: 2em 1em ;
		text: {
			indent: 2em;
		}
		background:{
			image: url("./ico32-book.png");
			repeat: no-repeat;
			position: left center ;
			size: 14px 14px ;
		}
	}
	
	
	.thumb {
		display: inline-block;
		width: 400px;
		//height: 300px;
		margin: 1em 30px;
		text-align: center;
		//float: left;
		
		img {
			max-width: 100%;
			max-height: 100%;
		}
	}
	
	.mui-popup-buttons{
		display: block;
		height: 2em;
		margin: 1em;
	}
}

